import Vue from 'vue'
import Vuex from 'vuex'
import * as types from './types'
import {CONFIG} from '../assets/js/config/index.js' // 获取配置信息

Vue.use(Vuex)
Vue.use(CONFIG); // 注册全局配置信息
const vm = new Vue();

const debug = process.env.NODE_ENV !== 'production'

export default new Vuex.Store({
  state: {
    listShow: true,
    list: vm.$storage.getItems(),
    done: 0,
    allWorks: null,
    addShow: false,
    active: 0,
    shadeShow: false,
    editStatus: false,
    editIndex: null
  },
  mutations: {
    [types.SHOW_DIALOG](state, s){
      state.addShow = s;
    },
    [types.SET_ACTIVE](state, s){
      if (state.active > 1 || !state.addShow) {
        state.active = s;
      } else {
        state.active += s;
      }
    },
    [types.ADD_LIST](state, s){
      if (state.editStatus) {
        state.list.splice(state.editIndex, 1, s);
      } else {
        state.list.push(s);
      }
    },
    [types.SHOW_SHADE](state, s){
      state.shadeShow = s;
    },
    [types.SET_DONE](state, s){
      s === '+1' ? state.done++ : state.done = s;
      //state.done += s;
    },
    [types.SET_ITEM_DONE](state, s){
      state.list.forEach((item, index) => {
        if (index == s) {
          item.isFinish = true;
        }
      })
    },
    [types.DEL_ITEM](state, s){
      state.list.reverse().splice(s, 1);
    },
    [types.EDIT_ITEM](state, s){
      state.editStatus = s;
    },
    editIndex(state, s){
      state.editIndex = s;
    }
  },
  actions: {
    [types.SHOW_DIALOG]({commit}, s){
      commit(types.SHOW_DIALOG, s);
    },
    [types.SET_ACTIVE]({commit, state}, s){
      commit(types.SET_ACTIVE, s);
    },
    [types.ADD_LIST](state, s){
      commit(types.ADD_LIST, s);
    },
    [types.SHOW_SHADE]({commit}, s){
      commit(types.SHOW_SHADE, s);
    },
    [types.SET_DONE]({commit}, s){
      commit(types.SET_DONE, s);
    },
    [types.SET_ITEM_DONE]({commit}, s){
      commit(types.SET_ITEM_DONE, s);
    },
    [types.DEL_ITEM]({commit}, s){
      commit(types.DEL_ITEM, s);
    },
    [types.EDIT_ITEM]({commit}, s){
      commit(types.EDIT_ITEM, s);
    }
  },
  modules: {},
  strict: debug,
})



