const STORAGE_KEY = 'shadowList'
let storage = new class {
  constructor(items) {
    this.items = items;
  }

  getItems() {
    return JSON.parse(window.localStorage.getItem(STORAGE_KEY)) ? JSON.parse(window.localStorage.getItem(STORAGE_KEY)) : []
  }

  saveItems(items) {
    window.localStorage.setItem(STORAGE_KEY, JSON.stringify(items));
  }

}

export {storage}



